/* stylelint-enable */
.woocommerce-page .wrap {
  margin: 0; }

.woocommerce-page #wpcontent,
.woocommerce-page.woocommerce_page_wc-admin #wpbody-content {
  padding: 0;
  overflow-x: hidden !important;
  min-height: calc(100vh - 32px); }

@media (min-width: 783px) {
  .woocommerce-page #wpbody-content {
    padding-left: 0; } }

@media (max-width: 782px) {
  .woocommerce-page .wp-responsive-open #woocommerce-embedded-root,
  .woocommerce-page .wp-responsive-open #wpbody {
    position: relative;
    right: -14.5em; }
  .woocommerce-page #wpcontent,
  .woocommerce-page #wpbody-content {
    min-height: calc(100vh - 46px); } }

@media (min-width: 961px) {
  .woocommerce-page #toplevel_page_woocommerce.menu-top > a:focus,
  .woocommerce-page #toplevel_page_wcadmin--analytics.menu-top > a:focus {
    padding-bottom: 1px; } }

.woocommerce-layout * {
  box-sizing: border-box; }

#wpbody {
  display: inline-block;
  width: 100%;
  box-sizing: border-box;
  padding-top: 0;
  margin-top: 60px; }

#wpfooter {
  display: none; }

.woocommerce_page_wc-admin .woocommerce-filters-date__content:not(.is-mobile) {
  z-index: 2;
  /* below of woocommerce-layout__header */ }

@media (max-width: 600px) {
  #wpadminbar {
    position: fixed; }
  html.wp-toolbar {
    padding-top: 46px; } }

@media (max-width: 782px) {
  .jetpack-masterbar #wpadminbar #wp-admin-bar-menu-toggle {
    margin-top: -10px; }
  .jetpack-masterbar #wpwrap .woocommerce-layout__header-heading {
    padding-left: 60px; }
  .jetpack-masterbar.wp-admin .wrap h1,
  .jetpack-masterbar.wp-admin .wrap h2 {
    padding-left: 0; } }

.woocommerce-page .wp-has-current-submenu::after {
  right: 0;
  border: 8px solid transparent;
  content: ' ';
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-right-color: #f1f1f1;
  top: 0;
  margin-top: 10px; }
  @media (max-width: 960px) {
    .woocommerce-page .wp-has-current-submenu::after {
      border-width: 4px;
      margin-top: 14px; } }

:root {
  --large-gap: 40px;
  --main-gap: 24px; }

@media (max-width: 960px) {
  :root {
    --large-gap: 24px; } }

@media (max-width: 782px) {
  :root {
    --large-gap: 16px;
    --main-gap: 16px; } }

@keyframes loading-fade {
  0% {
    opacity: 0.7; }
  50% {
    opacity: 1; }
  100% {
    opacity: 0.7; } }

.woocommerce-layout select:hover {
  color: #1e1e1e; }

.woocommerce-layout select.components-select-control__input {
  max-width: 100%;
  line-height: 1; }

body.woocommerce-page .components-button.is-primary:not(:disabled) {
  color: #fff; }

body.woocommerce-page .components-snackbar .components-button.is-tertiary {
  color: #fff; }
  body.woocommerce-page .components-snackbar .components-button.is-tertiary:not(:disabled):not([aria-disabled='true']):hover {
    color: #fff; }

.woocommerce-embed-page {
  /**
	* Hides screen meta and links when notices are open since the elements get moved around.
	* The !important overwrites an inline wp-admin style.
	*/ }
  .woocommerce-embed-page #wpbody .woocommerce-layout,
  .woocommerce-embed-page .woocommerce-layout__notice-list-hide + .wrap {
    padding-top: 10px; }
  .woocommerce-embed-page #wpcontent,
  .woocommerce-embed-page #wpbody-content {
    overflow-x: initial !important; }
  .woocommerce-embed-page #wpbody-content {
    padding-top: 0; }
  .woocommerce-embed-page #wpbody-content .notice {
    margin-top: 15px; }
  .woocommerce-embed-page .wrap {
    padding: 0 20px; }
    @media (max-width: 782px) {
      .woocommerce-embed-page .wrap p.search-box {
        width: calc(100% - 40px); } }
    .woocommerce-embed-page .wrap .wrap {
      padding: 0; }
  .woocommerce-embed-page #screen-meta {
    border-right: 0;
    margin: 0; }
  .woocommerce-embed-page #screen-meta-links {
    position: relative; }
  .woocommerce-embed-page .notice {
    padding: 1px 12px; }
  .woocommerce-embed-page .woocommerce-layout__header.is-scrolled {
    box-shadow: 0 8px 16px 0 rgba(85, 93, 102, 0.3); }
  .woocommerce-embed-page .woocommerce-layout__header .woocommerce-layout__header-heading {
    margin-top: 0;
    margin-bottom: 0; }
  .woocommerce-embed-page #screen-meta.is-hidden-by-notices,
  .woocommerce-embed-page #screen-meta-links.is-hidden-by-notices {
    display: none !important; }
  .woocommerce-embed-page .woocommerce-layout__primary {
    margin: 0; }
    @media (max-width: 782px) {
      .woocommerce-embed-page .woocommerce-layout__primary {
        padding-top: 10px; } }

@keyframes isLoaded {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
  .woocommerce-embed-page .woocommerce-layout__activity-panel-tabs {
    animation: isLoaded;
    animation-duration: 2000ms; }
  .woocommerce-embed-page .woocommerce-layout__notice-list-show {
    margin-top: 10px;
    margin-bottom: 16px; }
    @media (max-width: 600px) {
      .woocommerce-embed-page .woocommerce-layout__notice-list-show {
        margin-top: 80px;
        margin-bottom: -16px; } }
    @media (min-width: 601px) and (max-width: 782px) {
      .woocommerce-embed-page .woocommerce-layout__notice-list-show {
        margin-top: 32px; } }
  .woocommerce-embed-page .woocommerce-activity-card__actions a.components-button:not(.is-primary) {
    color: #757575; }

:root {
  --wp-admin-theme-color: #007cba;
  --wp-admin-theme-color-darker-10: #006ba1;
  --wp-admin-theme-color-darker-20: #005a87; }
